        dose2: null,
        time2: null,
        initial2: null,
        dose3: null,
        time3: null,
        initial3: null,
        dose4: null,
        time4: null,
        initial4: null,
        dose5: null,
        time5: null,
        initial5: null,
        dose6: null,
        time6: null,
        initial6: null,
        dose7: null,
        time7: null,
