export function useFormatToDrugLine(line) {
    let lineStr = "";
    for (let index = 1; index < 8; index++) {
        lineStr += line[`dose${index}`] + "~";
        lineStr += line[`time${index}`] + "~";
        lineStr += line[`initial${index}`];
        if (index != 7) lineStr += "|";
    }
    return lineStr;
}

export function useBpO2PulseLineFormatter(line) {
    let lineStr = "";
    for (let index = 1; index < 18; index++) {
        lineStr += line[`timeInit${index}`] + (index != 17 ? "|" : "");
    }
    return lineStr;
}

export function useTineLineFormatter(line) {
    let lineStr = "";
    for (let index = 1; index < 18; index++) {
        lineStr += line[`timeInit${index}`].part1 + "~";
        lineStr += line[`timeInit${index}`].part2 + (index != 17 ? "|" : "");
    }
    return lineStr;
}

export function useLoadIinitalFormData({ opuForm, store }) {
    return () => {
        opuForm.opuId = store.state.opu.opuId;
        opuForm.cycleId = store.state.opu.cycleId;
        opuForm.patientId = store.state.opu.patientId;
        opuForm.docDate = store.state.opu.docDate;
        opuForm.ativanLine = useFormatToDrugLine(store.state.opu.ativanLine);
        opuForm.versedLine = useFormatToDrugLine(store.state.opu.versedLine);
        opuForm.fentanylLine = useFormatToDrugLine(
            store.state.opu.fentanylLine
        );
        opuForm.narcanLine = useFormatToDrugLine(store.state.opu.narcanLine);
        opuForm.atropineLine = useFormatToDrugLine(
            store.state.opu.atropineLine
        );
        opuForm.gravolLine = useFormatToDrugLine(store.state.opu.gravolLine);
        opuForm.xylocaineLine = useFormatToDrugLine(
            store.state.opu.xylocaineLine
        );
        opuForm.other1Line = useFormatToDrugLine(store.state.opu.other1Line);
        opuForm.other2Line = useFormatToDrugLine(store.state.opu.other2Line);
        opuForm.other3Line = useFormatToDrugLine(store.state.opu.other3Line);
        opuForm.other4Line = useFormatToDrugLine(store.state.opu.other4Line);
        opuForm.other5Line = useFormatToDrugLine(store.state.opu.other5Line);
        opuForm.other6Line = useFormatToDrugLine(store.state.opu.other6Line);
        opuForm.other1Name = store.state.opu.other1Name;
        opuForm.other2Name = store.state.opu.other2Name;
        opuForm.other3Name = store.state.opu.other3Name;
        opuForm.other4Name = store.state.opu.other4Name;
        opuForm.other5Name = store.state.opu.other5Name;
        opuForm.other6Name = store.state.opu.other6Name;

        opuForm.preTimeLine = useTineLineFormatter(store.state.opu.preTimeLine);
        opuForm.preBPLine = useBpO2PulseLineFormatter(
            store.state.opu.preBPLine
        );
        opuForm.preO2Line = useBpO2PulseLineFormatter(
            store.state.opu.preO2Line
        );
        opuForm.prePulseLine = useBpO2PulseLineFormatter(
            store.state.opu.prePulseLine
        );

        opuForm.postTimeLine = useTineLineFormatter(
            store.state.opu.postTimeLine
        );
        opuForm.postBPLine = useBpO2PulseLineFormatter(
            store.state.opu.preBPLine
        );
        opuForm.postO2Line = useBpO2PulseLineFormatter(
            store.state.opu.postO2Line
        );
        opuForm.postPulseLine = useBpO2PulseLineFormatter(
            store.state.opu.postPulseLine
        );
    };
}
