<template>
    <p-card>
        <template #content>
            <div class="p-grid">
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label for="date" class="p-col-fixed p-mb-0">
                            Date
                        </label>
                        <div class="p-col">
                            <p-calendar
                                v-model="form.docDate"
                                id="date"
                                dateFormat="yy-mm-dd"
                            />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label for="md" class="p-col-fixed p-mb-0">
                            MD
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="md"
                                v-model="form.md"
                                :options="mdOptions"
                                :filter="true"
                            />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label for="iv-started" class="p-col-fixed p-mb-0">
                            IV Started
                        </label>
                        <div class="p-col">
                            <p-input-mask
                                v-model="form.ivStart"
                                id="iv-started"
                                mask="99:99:99"
                                slotChar="00:00:00"
                            />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label for="by" class="p-col-fixed p-mb-0">
                            By
                        </label>
                        <div class="p-col">
                            <p-dropdown
                                id="by"
                                v-model="form.by"
                                :options="byOntions"
                                :filter="true"
                            />
                        </div>
                    </div>
                </div>

                <div class="p-col">
                    <p-button
                        label="Pre-OP Nursing Assessment"
                        class="p-mr-1"
                        @click="
                            openForm(
                                'pre-operative-nursing-assessment-opu'
                            )
                        "
                    />
                    <p-button
                        label="Pre-op Procedural Safety Checklist"
                        class="p-mr-1"
                        @click="openForm('procedural-safety-checklist')"
                    />
                    <p-button
                        label="Pre-Anesthelic Assessment"
                        class="p-mr-1"
                        @click="
                            openForm(
                                'newlife-fertility-centre-anesthetic-record'
                            )
                        "
                    />
                    <p-button
                        label="Orders"
                        class="p-mr-1"
                        @click="openForm('opu-orders')"
                    />
                    <p-button
                        label="Post-OP"
                        class="p-mr-1"
                        @click="openForm('post-sedation-discharge-form')"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, onMounted, reactive, watch } from "vue";
import { useStore } from "vuex";
import format from "date-fns/format";
import { FORM_BASE } from "@/constants";
export default {
    setup() {
        const store = useStore();
        const topForm = computed(() => store.getters["opu/getTopForm"]);
        const mdOptions = reactive(["SS", "SD"]);
        const byOntions = reactive(["MD", "RN"]);

        const form = reactive({
            docDate: null,
            ivStart: null,
            md: null,
            by: null,
            patientId: null,
            cycleId: null,
            opuId: null
        });

        const opuForm = inject("opuForm");
        watch(form, (newValue) => {
            opuForm.docDate = newValue.docDate
                ? format(new Date(newValue.docDate), "yyyy-MM-dd")
                : null;
            opuForm.ivStart = newValue.ivStart;
            opuForm.md = newValue.md;
            opuForm.givenBy = newValue.by;
            opuForm.patientId = newValue.patientId;
            opuForm.cycleId = newValue.cycleId;
            opuForm.opuId = newValue.opuId;
        });

        const fillupFormData = () => {
            form.docDate = topForm.value.docDate;
            form.ivStart = topForm.value.ivStart;
            form.md = topForm.value.md;
            form.by = topForm.value.by;
            form.patientId = topForm.value.patientId;
            form.cycleId = topForm.value.cycleId;
            form.opuId = topForm.value.opuId;
        };

        const formSrc = inject("formSrc");

        const openForm = (formName) => {
            const patientId = store.state.artCycle.selectedCycle.patientId;
            const userName = store.state.user.username
            const url = `${FORM_BASE}?patient_id=${patientId}&form_name=${formName}&user_name=${userName}`;
            formSrc.value = url;

            // window.open(url);
        };

        watch(topForm, () => {
            fillupFormData();
        });

        onMounted(() => {
            fillupFormData();
        });

        return { form, mdOptions, byOntions, openForm };
    }
};
</script>
