<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-fixed">
                    <div class="p-formgroup-inline">
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.upToVoid"
                                :binary="true"
                            />
                            <label for="city3">Up to Void</label>
                        </div>

                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.fluidsPo"
                                :binary="true"
                            />
                            <label for="city4">Fluids PO</label>
                        </div>

                        <div class="p-field-checkbox">
                            <p-checkbox v-model="form.cramps" :binary="true" />
                            <label for="city4">Cramps</label>
                        </div>

                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.bleeding"
                                :binary="true"
                            />
                            <label for="city4">Bleeding Checked</label>
                        </div>

                        <div class="p-field-checkbox">
                            <p-checkbox v-model="form.nausea" :binary="true" />
                            <label for="city4">Nausea/Vomiting</label>
                        </div>

                        <div class="p-field p-grid">
                            <label class="p-col-fixed p-mb-0">
                                IV Disconnect
                            </label>
                            <div class="p-col">
                                <p-calendar
                                    v-model="form.ivDisconnect"
                                    :timeOnly="true"
                                    hourFormat="12"
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="p-fluid p-grid">
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Amount of Fluid Given
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.fluidGiven" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-formgroup-inline">
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.discharged"
                                :binary="true"
                            />
                            <label>Discharged home with instructions</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-checkbox
                                v-model="form.accompanied"
                                :binary="true"
                            />
                            <label>Accompanied</label>
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            RN/RPN
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.rnRna" />
                        </div>
                    </div>
                </div>
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            MD
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.mdSummary" />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, onMounted, reactive, watch } from "vue";
import { useStore } from "vuex";
import { format } from "date-fns";
export default {
    setup() {
        const store = useStore();
        const bottonForm = computed(() => store.getters["opu/getBottomForm"]);
        const form = reactive({
            upToVoid: false,
            fluidsPo: false,
            cramps: false,
            bleeding: false,
            nausea: false,
            discharged: false,
            accompanied: false,
            rnRna: null,
            mdSummary: null,
            ivDisconnect: null,
            fluidGiven: null
        });

        const opuForm = inject("opuForm");

        watch(form, (newValue) => {
            opuForm.upToVoid = newValue.upToVoid;
            opuForm.fluidsPo = newValue.fluidsPo;
            opuForm.cramps = newValue.cramps;
            opuForm.bleeding = newValue.bleeding;
            opuForm.nausea = newValue.nausea;
            opuForm.discharged = newValue.discharged;
            opuForm.accompanied = newValue.accompanied;
            opuForm.rnRna = newValue.rnRna;
            opuForm.mdSummary = newValue.mdSummary;
            opuForm.ivDisconnect = newValue.ivDisconnect
                ? format(newValue.ivDisconnect, "h:mm a")
                : null;
            opuForm.fluidGiven = newValue.fluidGiven;
        });

        const fillupFormData = () => {
            form.upToVoid = bottonForm.value.upToVoid;
            form.fluidsPo = bottonForm.value.fluidsPo;
            form.cramps = bottonForm.value.cramps;
            form.bleeding = bottonForm.value.bleeding;
            form.nausea = bottonForm.value.nausea;
            form.discharged = bottonForm.value.discharged;
            form.accompanied = bottonForm.value.accompanied;
            form.rnRna = bottonForm.value.rnRna;
            form.mdSummary = bottonForm.value.mdSummary;
            form.ivDisconnect = bottonForm.value.ivDisconnect;
            form.fluidGiven = bottonForm.value.fluidGiven;
        };

        watch(bottonForm, () => {
            fillupFormData();
        });

        onMounted(() => {
            fillupFormData();
        });

        return { form, options: reactive([]) };
    }
};
</script>
