<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            O2
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.o2" style="width: 40px" />
                        </div>
                        <label class="p-col-fixed p-mb-0">
                            L
                        </label>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Via
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.o2via" style="width: 40px" />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Comments
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.comment" style="width: 400px" />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Number of eggs retrived
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.eggsNum" style="width: 60px" />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Return to recovery
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.recovery" style="width: 80px" />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Bp
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.bpSummary" style="width: 40px" />
                        </div>
                    </div>
                </div>

                <div class="p-col-fixed">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed p-mb-0">
                            Pulse
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.pulseSummary" style="width: 40px" />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, onMounted, reactive, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const middleForm = computed(() => store.getters["opu/getMiddleForm"]);
        const form = reactive({
            o2: null,
            o2via: null,
            comment: null,
            eggsNum: null,
            recovery: null,
            bpSummary: null,
            pulseSummary: null
        });

        const fillupFormData = () => {
            form.o2 = middleForm.value.o2;
            form.o2via = middleForm.value.o2via;
            form.comment = middleForm.value.comment;
            form.eggsNum = middleForm.value.eggsNum;
            form.recovery = middleForm.value.recovery;
            form.bpSummary = middleForm.value.bpSummary;
            form.pulseSummary = middleForm.value.pulseSummary;
        };

        const opuForm = inject("opuForm");

        watch(form, (newValue) => {
            opuForm.o2 = newValue.o2;
            opuForm.o2via = newValue.o2via;
            opuForm.comment = newValue.comment;
            opuForm.eggsNum = newValue.eggsNum;
            opuForm.recovery = newValue.recovery;
            opuForm.bpSummary = newValue.bpSummary;
            opuForm.pulseSummary = newValue.pulseSummary;
        });

        watch(middleForm, () => {
            fillupFormData()
        })

        onMounted(() => {
            fillupFormData();
        });

        return { form };
    }
};
</script>
