<script>
import { useOocyteCollectionInit } from "@/composable/born";
import { inject, onMounted, toRefs, watch } from "vue-demi";
import format from "date-fns/format";
import { DATE_FORMAT_STRING } from "@/config";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            loading,
            v$,
            save,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions
        } = useOocyteCollectionInit();

        const { isInBornPage } = toRefs(props);

        watch(() => store.state.born.oocyteCollections, fillForm);

        watch(
            () => form.retrievedOrThawed,
            (newVal) => {
                store.commit("born/SET_OOCYTES_RETRIEVED", newVal);
            }
        );

        const bornForm = inject("bornForm", null);
        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.oocyteCollection = {
                ...newValue,
                donorProviderSource: form.oocytesProviderSource,
                oocyteCollectionDate: form.oocyteCollectionDate
                    ? format(
                          new Date(form.oocyteCollectionDate),
                          DATE_FORMAT_STRING
                      )
                    : null,
                oocyteThawDate: form.oocyteThawDate
                    ? format(new Date(form.oocyteThawDate), DATE_FORMAT_STRING)
                    : null,
                oocyteFreezeDate: form.oocyteFreezeDate
                    ? format(
                          new Date(form.oocyteFreezeDate),
                          DATE_FORMAT_STRING
                      )
                    : null
            };
        });

        onMounted(() => {
            if (isInBornPage.value) {
                return;
            }

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        return {
            isInBornPage,
            form,
            loading,
            save,
            v$,
            fillForm,
            oocyteOriginOptions,
            donorProviderStatusOptions,
            oocyteCryoMethodOptions,
            spermOriginOptions,
            spermOriginStatusOptions,
            spermSourceOptions
        };
    }
};
</script>

