<script>
import { inject, onMounted, onUnmounted, toRefs } from "vue";
import { emitter } from "@/event-bus";
import {
    useBpO2PulseLineFormatter,
    usePatientChart,
    useTineLineFormatter
} from "@/composable/opu";
import { useStore } from "vuex";
export default {
    props: {
        type: String
    },
    setup(props) {
        const store = useStore();
        const patientChart = usePatientChart();
        const { type } = toRefs(props);

        const loadChartData = () => {
            if (props.type === "pre") {
                patientChart.value = store.getters["opu/getPrePatientChart"];
            }
            if (props.type === "post") {
                patientChart.value = store.getters["opu/getPostPatientChart"];
            }
        };

        const opuForm = inject("opuForm");
        const formatToLine = (data) => {
            if (data.key === "preBpLine") {
                opuForm.preBPLine = useBpO2PulseLineFormatter(data);
            } else if (data.key === "preO2Line") {
                opuForm.preO2Line = useBpO2PulseLineFormatter(data);
            } else if (data.key === "prePulseLine") {
                opuForm.prePulseLine = useBpO2PulseLineFormatter(data);
            } else if (data.key === "postBpLine") {
                opuForm.postBPLine = useBpO2PulseLineFormatter(data);
            } else if (data.key === "postPulseLine") {
                opuForm.postPulseLine = useBpO2PulseLineFormatter(data);
            } else if (data.key === "postO2Line") {
                opuForm.postO2Line = useBpO2PulseLineFormatter(data);
            } else if (data.key === "preTimeLine") {
                opuForm.preTimeLine = useTineLineFormatter(data);
            } else if (data.key === "postTimeLine") {
                opuForm.postTimeLine = useTineLineFormatter(data);
            }
        };

        onMounted(() => {
            emitter.on("opu:summary-loaded", loadChartData);
        });

        onUnmounted(() => {
            emitter.off("opu:summary-loaded", loadChartData);
        });

        return { patientChart, type, formatToLine };
    }
};
</script>

